/* Piet Hello World - Interactive Code Art */
.App {
  text-align: center;
  background: #000000;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  /* Temporarily show cursor to debug */
}

/* Piet Container */
.piet-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
}

.piet-title {
  color: #ffffff;
  font-family: 'Courier New', monospace;
  font-size: 2.5rem;
  font-weight: bold;
  margin: 0;
  text-shadow: 0 0 20px rgba(255, 255, 255, 0.5);
}

/* Piet Grid */
.piet-grid {
  display: grid;
  grid-template-columns: repeat(13, 1fr);
  gap: 1px;
  background: #000000;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 0 40px rgba(255, 255, 255, 0.2);
}

/* Piet Cells - completely static, no movement */
.piet-cell {
  width: 25px;
  height: 25px;
  transition: none !important;
  transform: none !important;
  animation: none !important;
  position: static !important;
}

/* Disable any hover effects */
.piet-cell:hover {
  transform: none !important;
  transition: none !important;
  animation: none !important;
}

/* Exact Piet colors */
.piet-cell.red {
  background-color: #FF0000;
}

.piet-cell.green {
  background-color: #00FF00;
}

.piet-cell.blue {
  background-color: #0000FF;
}

.piet-cell.cyan {
  background-color: #00FFFF;
}

.piet-cell.magenta {
  background-color: #FF00FF;
}

.piet-cell.yellow {
  background-color: #FFFF00;
}

.piet-cell.white {
  background-color: #FFFFFF;
}

.piet-cell.black {
  background-color: #000000;
  border: 1px solid #333333;
}

/* Light Piet colors */
.piet-cell.light-red {
  background-color: #FFC0C0;
}

.piet-cell.light-green {
  background-color: #C0FFC0;
}

.piet-cell.light-blue {
  background-color: #C0C0FF;
}

.piet-cell.light-cyan {
  background-color: #C0FFFF;
}

.piet-cell.light-magenta {
  background-color: #FFC0FF;
}

.piet-cell.light-yellow {
  background-color: #FFFFC0;
}

/* Dark Piet colors */
.piet-cell.dark-red {
  background-color: #C00000;
}

.piet-cell.dark-green {
  background-color: #00C000;
}

.piet-cell.dark-blue {
  background-color: #0000C0;
}

.piet-cell.dark-cyan {
  background-color: #00C0C0;
}

.piet-cell.dark-magenta {
  background-color: #C000C0;
}

.piet-cell.dark-yellow {
  background-color: #C0C000;
}

/* No hover effects - disabled for clean Blobity experience */

.piet-info {
  color: #ffffff;
  font-family: 'Courier New', monospace;
  text-align: center;
}

.piet-info p {
  margin: 5px 0;
  font-size: 1.1rem;
  opacity: 0.8;
}

/* Cursor will be handled by Blobity when it loads */

/* Responsive design */
@media (max-width: 768px) {
  .piet-grid {
    grid-template-columns: repeat(20, 1fr);
    gap: 0.5px;
    padding: 5px;
  }

  .piet-cell {
    width: 15px;
    height: 15px;
  }

  .piet-title {
    font-size: 2rem;
  }
}